.form {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 40px;
}

.title {

}

.section {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: #000000;
}

.section .color {
    position: absolute;
    filter: blur(150px);
}

.section .color:nth-child(1) {
    top: 250px;
    width: 1000px;
    height: 600px;
    background: #ff359b;
}

.section .color:nth-child(2) {
    bottom: 50px;
    right: 100px;
    width: 900px;
    height: 600px;
    background: #fffd87;
}

.section .color:nth-child(3) {
    bottom: 50px;
    right: 100px;
    width: 300px;
    height: 500px;
    background: #00d2ff;
}

.section .color:nth-child(4) {
    bottom: 50px;
    left: 50px;
    width: 600px;
    height: 700px;
    background: #b700ff;
}

.section .color:nth-child(5) {
    bottom: 250px;
    left: 450px;
    width: 350px;
    height: 400px;
    background: #ffffff;
}

.box {
    position: relative;
}

.box .square {
    position: absolute;
    backdrop-filter: blur(5px);
    box-shadow: 0 25px 45px rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-right: 1px solid rgba(255, 255, 255, 0.2);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    animation: animate 10s linear infinite;
    animation-delay: calc(-1s);
    rotate: 45deg;

}

.box .square:hover {
    box-shadow: 0 0 15px 1px white;

}

@keyframes animate {
    0%, 100% {
        transform: translateY(-40px);
    }
    50% {
        transform: translateY(40px);
    }

}

.box .square:nth-child(1) {
    top: -70px;
    right: -90px;
    width: 100px;
    height: 100px;
    border-bottom-left-radius: 10%;
    border-bottom-right-radius: 10%;
    border-top-left-radius: 10%;
    border-top-right-radius: 10%;
    rotate: 90deg;
}


.box .square:nth-child(2) {
    top: 150px;
    right: 580px;
    width: 120px;
    height: 120px;
    z-index: 2;
    border-radius: 10%;
    rotate: 450deg;
}

.box .square:nth-child(3) {
    bottom: 45px;
    right: -30px;
    width: 50px;
    height: 150px;
    z-index: 2;
    rotate: 0deg;
    border-radius: 70%;
}

.box .square:nth-child(4) {
    bottom: -100px;
    left: 100px;
    width: 80px;
    height: 80px;
    z-index: 2;
    rotate: 0deg;
    background: url("../../../images/icon/burger.svg");
    background-repeat: no-repeat;
    background-size: cover;
}

.box .square:nth-child(5) {
    top: -50px;
    left: 90px;
    width: 60px;
    height: 60px;
    z-index: 2;
    rotate: 280deg;
    border-radius: 50%;
    background: url("../../../images/icon/burger.svg");
    background-repeat: no-repeat;
    background-size: contain;
}

.box .square:nth-child(6) {
    bottom: 200px;
    left: 800px;
    width: 80px;
    height: 80px;
    z-index: 2;
    rotate: 0deg;
    border-radius: 50%;
    background: url("../../../images/icon/burger.svg");
    background-repeat: no-repeat;
    background-size: contain;
}

.box .square:nth-child(7) {
    top: 250px;
    right: 780px;
    width: 100px;
    height: 100px;
    z-index: 2;
    border-radius: 50%;
    rotate: 180deg;
    background: url("../../../images/icon/burger.svg");
    background-repeat: no-repeat;
    background-size: contain;
}

.container {
    position: relative;
    width: 580px;
    min-height: 430px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(5px);
    box-shadow: 0 25px 45px rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-right: 1px solid rgba(255, 255, 255, 0.2);
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}


.form .inputBox {
    width: 100%;
    margin-top: 20px;
    padding-bottom: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.forget {
    margin-top: 5px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
}

.forget a {
    color: #002bff;
    font-weight: 600;
    text-decoration: none;
}

h2 {
    display: flex;
    align-items: center;
    justify-content: center;
}
