.constructorDropzoneWrapper {
    flex: 1;
    overflow-y: auto;
    padding: 15px 0;
}

.constructorDropzoneWrapper::-webkit-scrollbar {
    width: 8px;
}

.constructorDropzoneWrapper::-webkit-scrollbar-track {
    background: var(--background-element);
}

.constructorDropzoneWrapper::-webkit-scrollbar-thumb {
    background: var(--text-inactive-color);
}

.constructorDropzoneWrapper > *:not(:last-of-type) {
    margin-bottom: 16px;
}

.cleanDropzone {
    border: 1px dashed var(--text-inactive-color);
    border-radius: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 55px auto;
    width: 350px;
    cursor: pointer;
}

.cleanDropzone:hover {
    border: 1px solid transparent;
    box-shadow: rgb(133, 133, 173) 0 0 15px 1px;
}

.hoveredCleanDropzone {
    border: 1px solid transparent;
    box-shadow: #00f7ff 0 0 35px 1px;
}


.span {
    animation: animate 1.9s linear infinite;
}

.span:nth-child(1) {
    animation-delay: 0.1s;
}

.span:nth-child(2) {
    animation-delay: 0.2s;
}

.span:nth-child(3) {
    animation-delay: 0.3s;
}

.span:nth-child(4) {
    animation-delay: 0.4s;
}

.span:nth-child(5) {
    animation-delay: 0.5s;
}

.span:nth-child(6) {
    animation-delay: 0.6s;
}

.span:nth-child(7) {
    animation-delay: 0.7s;
}

.span:nth-child(8) {
    animation-delay: 0.8s;
}

.span:nth-child(9) {
    animation-delay: 0.9s;
}

.span:nth-child(10) {
    animation-delay: 1s;
}

.span:nth-child(11) {
    animation-delay: 1.1s;
}

.span:nth-child(12) {
    animation-delay: 1.2s;
}

.span:nth-child(13) {
    animation-delay: 1.3s;
}

.span:nth-child(14) {
    animation-delay: 1.4s;
}

.span:nth-child(15) {
    animation-delay: 1.5s;
}

.span:nth-child(16) {
    animation-delay: 1.6s;
}

.span:nth-child(17) {
    animation-delay: 1.7s;
}

.span:nth-child(18) {
    animation-delay: 1.8s;
}

.span:nth-child(19) {
    animation-delay: 1.9s;
}

@keyframes animate {
    0%, 80% {
        color: var(--text-inactive-color);
        text-shadow: none;
        line-height: 1.5;

    }
    100% {
        line-height: 1.6;
        color: #fff;
        text-shadow: 0 0 10px #ff00cb,
                     0 0 20px #ff00cb,
                     0 0 40px #ff00cb,
                     0 0 80px #ff00cb,
                     0 0 120px #ff00cb,
                     0 0 160px #ff00cb;
    }
}

.spanN {
    animation: animateN 2s linear infinite;
}

@keyframes animateN {
    0%, 80% {
        color: var(--text-inactive-color);
        text-shadow: none;
    }
    100% {

        color: #fff;
        text-shadow: 0 0 10px #00a6ff,
                     0 0 20px #00a6ff,
                     0 0 40px #00a6ff,
                     0 0 80px #00a6ff,
                     0 0 120px #00a6ff,
                     0 0 260px #00a6ff;
    }
}
