.preLoader {
    animation: infinite linear 1.5s rotate;
    width: 100px;
    height: 100px;
    box-shadow: rgb(153, 0, 255) 0 0 150px 15px;
    border-radius: 50%;
}

@keyframes rotate {
    from {
        rotate: 0deg;
    }
    to {
        rotate: 360deg;
    }
}

.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}
