.burgerIngredients {
    max-width: 600px;
    user-select: none;
}

.tabs {
    display: flex;
}

.ingredients {
    height: calc(100vh - 248px);
    overflow-y: scroll;
}

.ingredients::-webkit-scrollbar {
    width: 8px;
}

.ingredients::-webkit-scrollbar-thumb {
    background: var(--text-inactive-color);
}

.ingredients::-webkit-scrollbar-track {
    background: var(--background-element);
}

