.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.text {
    padding: 0;
    margin-top: 56px;
}

.form {
    padding-bottom: 15px;
    padding-right: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;
}

.link {
    text-decoration: none;
    color: #4C4CFF;
}
